import React, { useContext } from 'react'
import { useNavigate } from 'react-router-dom'
import { Qscontext } from '../App'
import Left from './Left.svg'
import Right from './right.svg'
import Back from './home.svg'

function I_Button({ a }) {
    const qs = useContext(Qscontext)
    const navigate = useNavigate()
    const goHome = () => {
        navigate('/')
    }


    const Next = () => {
        const next = qs[nextId].path
        navigate(next)
    }

    const nextId = a.id + 1
    const prevId = a.id - 1

    const Prev = () => {
        if (prevId >= 0) {
            const prev = qs[prevId].path
            navigate(prev)
        }
    }
    return (
        <div className="i-back">
            {a.id === 0 ? null : <div className="prev" onClick={Prev}><img src={Left} alt="" /></div>}
            <div className="back" onClick={goHome}><img src={Back} alt="" /></div>
            {a.id === 5 ? null : <div className="next" onClick={Next}><img src={Right} alt="" /></div>}
        </div>
    )
}

export default I_Button
