import React, { useContext, useState } from 'react'
import { Link } from 'react-router-dom'
import { Qscontext } from '../App'
import './Qs.css'

function Qs() {
    const qs = useContext(Qscontext)
    const [modal, setmodal] = useState(false)

    const modalHandler = () => {
        setmodal(!modal)
    }

    return (
        <div className="qs">
            <div className="qs-title">Welcome to EVAS!</div>
            {qs.map((q) =>
                <Link to={q.path} key={q.id}>
                    <input type="button" value={"Q. " + q.q} className="i-btn q-btn" onClick={() => { setmodal(!modal) }} />
                </Link>
            )}
        </div >
    )
}

export default Qs
